.home-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  padding: 60px 20px;
  background-color: #f5fff5; /* light green/white blend */
  text-align: center;
}

.home-banner h1 {
  font-size: 48px;
  color: #2e7d32;
}

.home-banner p {
  font-size: 20px;
  margin: 15px 0;
  color: #555;
}
.home-btn {
  width: 300px;
  padding: 12px;
  background-color: transparent;
  border-radius: 5px;
  border-width: 0;
  color: #fff;
  outline: none;
}
.home-button-container {
  background-color: #2e7d32;
  color: #fff;
  padding: 12px 24px;
  text-decoration: none;
  border-radius: 6px;
  font-size: 16px;
  transition: background-color 0.3s ease; 
}

.home-button-container:hover {
  background-color: #1b5e20;
}
.wall-poster {
  height: 300px;
  width: 300px;
  border-radius: 50px;
}
